
import { Comment, Form, Button, Input , Card } from 'antd';
import axios from 'axios';
import { useEffect, useState } from 'react';

import { EditOutlined, DeleteOutlined } from '@ant-design/icons';


const Editor = ({
    onContentChange,
    onSubmit,
    submitting,
    blogContentValue,
    id }) => (
    <>
        <Form.Item>
            <Input.TextArea rows={4} onChange={onContentChange} value={blogContentValue} />
        </Form.Item>
        <Form.Item style={{ textAlign: "left" }}>
            <Button htmlType="submit" loading={submitting}
                onClick={() => onSubmit(id, blogContentValue)} type="primary" >
                Submit Changes
            </Button>
        </Form.Item>
    </>
);


const renderAction = (onEditClick , onDeleteClick ,id) => {
    return [
        <>
            <span onClick={() => onEditClick(true)}>
                {< EditOutlined />}
            </span>
            <span onClick={() => onDeleteClick(id)}>
                {< DeleteOutlined />}
            </span>
        </>
    ];
}


const BlogItem = ({ content, title, id, token, onDeleteBlog }) => {
    const [iseEditMode, toggleEditMode] = useState(false);
    const [blogContent, setBlogContent] = useState(content);

    const handleContentChange = e => {
        setBlogContent(e.target.value)
    };

    const onSubmitEdit = (id, newContent) => {
        axios.put(`/api/article/update/`, {
            content: newContent,
            id: id
        }, {
            headers: {
                "authorization": token
            }
        }).then(result => {
            toggleEditMode(false);
        })
    }

    const onDeleteClick = (id) => {
        axios.delete(`/api/article/delete/${id}`,{
            headers: {
                "authorization": token
            }
        }).then(
            onDeleteBlog(id)
        )
    }

    return (
        <div style={{ flex: "1 1 30%"}}>
        {!iseEditMode ? 
        <Card
          style={{ width: 300, marginTop: 16 }}>
          <Comment style={{ textAlign: "left" }}
            actions={renderAction(toggleEditMode, onDeleteClick,id)}
            content={
                <p>{blogContent}
                </p>
            } /> 
            </Card>:
            <Editor blogContentValue={blogContent}
                id={id} onSubmit={onSubmitEdit} onContentChange={handleContentChange} />}
    </div>
    )
}

export default BlogItem;
