
import { Comment, Form, Button, Input , Typography } from 'antd';
import axios from 'axios';
import { useEffect, useState } from 'react';

import BlogItem from './BlogItem';

const Editor = ({ onTitleChange, onContentChange,
  onSubmit, submitting, blogContentValue }) => (
  <>
    <Form.Item>
      <Input.TextArea rows={4} onChange={onContentChange} value={blogContentValue}  placeholder={"Start writing your new blog here"}/>
    </Form.Item>
    <Form.Item style={{ textAlign: "left" }}>
      <Button htmlType="submit" loading={submitting}
        onClick={onSubmit} type="primary" >
        Post New Blog
      </Button>
    </Form.Item>
  </>
);


const Blogs = (props) => {
  const [newBlog, setNewBlog] = useState("");
  const [isSubmitting, toggleSubmit] = useState("");
  const [allBlogs, setAllBlogs] = useState([])

  const handleSubmit = () => {
    toggleSubmit(true);
    axios.post("/api/article/addArticle", {
      name: props.user.name,
      content: newBlog,
      token: props.user.token
    }).then(result => {
      console.log(result)
      toggleSubmit(false)
      setNewBlog("")
      alert("Blog Added");
      setAllBlogs([...allBlogs, {
        name: result.data.name,
        content: result.data.content,
        createdOn: result.data.createdOn,
        id: result.data._id
      }]);
    }
    ).catch(result => {
      toggleSubmit(false)
      console.log(result.response)
    })
  };

  const handleContentChange = e => {
    setNewBlog(e.target.value)
  };

  useEffect(() => {
    console.log(props)
    axios.get("/api/article/", {
      headers: {
        "authorization": props.user.token
      }
    }).then(result => {
      console.log(result);
      setAllBlogs(result.data);
    })
  }, []);

  const onDeleteBlog = (id) => {
    setAllBlogs(allBlogs.filter(blog => blog.id !== id))
  }

  return (
    <div style={{ padding: "16px"}}>
      <Comment
        content={
          <Editor
            onContentChange={handleContentChange}
            onSubmit={handleSubmit}
            submitting={isSubmitting === true}
            blogContentValue={newBlog}
          />
        }
      />
      <div>
      <Typography.Title level={2}>My blogs</Typography.Title>
      </div>
      <div style={{display: "flex", gap: "12px" ,flexWrap: "wrap"}}>
        {
          allBlogs.sort((a,b) => a.createdOn > b.createdOn).map(blog => (
            <BlogItem key={blog.id} content={blog.content} title={blog.name}
              id={blog.id} token={props.user.token}
              onDeleteBlog={onDeleteBlog}
            />
          ))
        }
      </div>
    </div>

  );
};

export default Blogs;
